class CumulioService {
  components: any[] = [];
  /* ------------------
  | External Methods |
  ------------------*/

  // // Get all active dashboards
  getDashboards() {
    return Promise.resolve(this.components);
  }

  /* --------------------
  | Set Cumulio Object |
  --------------------*/

  _setComponentContext(vueComponentContext) {
    if (!this.components.includes(vueComponentContext)) {
      this.components.push(vueComponentContext);
    }
  }

  // remove component context from array after unmounting
  _removeComponentContext(vueComponentContext) {
    if (this.components.includes(vueComponentContext)) {
      this.components = this.components.filter(
        (context) => context !== vueComponentContext
      );
    }
  }
}
export default new CumulioService();
