import { VERSION, sanitizeUrl } from '../helpers/utils';

export const createIframeUrl = (dashboardContent) => {
  const searchstring = [];
  const {
    appServer,
    language,
    loader,
    screenMode,
    currentScreenMode,
    key,
    token,
    timezoneId,
    itemEmbedding,
    itemDimensions,
    dashboardId,
    dashboardSlug,
    itemId,
    mainColor,
    accentColor,
    editMode,
  } = dashboardContent.params;

  // language
  if (language && language !== 'auto')
    searchstring.push('language=' + language);

  // loader
  if (loader) {
    if (loader.background)
      searchstring.push('ldrbg=' + loader.background.replace('#', '--'));
    if (loader.fontColor)
      searchstring.push('ldrftclr=' + loader.fontColor.replace('#', '--'));
    if (loader.spinnerColor)
      searchstring.push('ldrspclr=' + loader.spinnerColor.replace('#', '--'));
    if (loader.spinnerBackground)
      searchstring.push(
        'ldrspbg=' + loader.spinnerBackground.replace('#', '--')
      );
  }

  if (mainColor) searchstring.push('mainColor=' + mainColor);
  if (accentColor) searchstring.push('accentColor=' + accentColor);

  // screenMode
  // const specifiedScreenMode = (screenMode && ['mobile', 'tablet', 'desktop', 'largeScreen', 'fixed'].includes(screenMode));
  // if (specifiedScreenMode && screenMode === currentScreenMode) searchstring.push('screenmode=' + currentScreenMode);
  if (screenMode && screenMode !== 'auto')
    searchstring.push('screenmode=' + screenMode);

  // key & token
  if (key && token && !editMode) {
    searchstring.push('key=' + key, 'token=' + token);
  }

  // timezoneId
  if (timezoneId) searchstring.push('timezoneId=' + timezoneId);

  // itemDimensions
  if (itemEmbedding && itemDimensions && itemDimensions.width)
    searchstring.push('width=' + itemDimensions.width);
  if (itemEmbedding && itemDimensions && itemDimensions.height)
    searchstring.push('height=' + itemDimensions.height);

  // Normal or beta route (i-beta/ to change to i/ route once MICE fully finished)
  const route = editMode ? 'i-beta/' : 'i/';
  if (editMode) searchstring.push('editMode=' + editMode);

  const integratedEditorKeyTokenFragment = key && token && editMode ? '#' + key + ':' + token : '';
  // version
  searchstring.push('version=' + (dashboardContent.VERSION || VERSION));
  const iframeUrl =
    itemEmbedding && !editMode
      ? appServer +
        route +
        (dashboardId || dashboardSlug) +
        '/' +
        itemId +
        '?' +
        searchstring.join('&')
      : appServer +
        route +
        (dashboardId || dashboardSlug) +
        '?' +
        searchstring.join('&') +
        integratedEditorKeyTokenFragment;
  return sanitizeUrl(iframeUrl);
};
