import { _handleError } from './error';

// check browser support: svg, postMessage, flexbox

export const _browserCheck = (dashboardComponent) => {
  let hasError = false;
  const error = { msg: '' };
  const browsercheck = { svg: false, flexbox: false, postMessage: false };
  browsercheck.svg = !!(
    'createElementNS' in document &&
    document.createElementNS('http://www.w3.org/2000/svg', 'svg').createSVGRect
  );
  const document_ = document.body || document.documentElement;
  const style = document_.style;
  if (
    style.webkitFlexWrap === '' ||
    style.flexWrap === '' ||
    style.flexWrap === ''
  )
    browsercheck.flexbox = true;

  browsercheck.postMessage = !!window.postMessage;
  for (const t of ['svg', 'flexbox', 'postMessage']) {
    if (!browsercheck[t]) {
      hasError = true;
      dashboardComponent.browserNotSupported =
        dashboardComponent.browserNotSupported || [];
      dashboardComponent.browserNotSupported.push(t);
      error.msg += 'Your browser does not support: ' + t + '\n';
    }
  }
  // Check browser for IE
  const ua = window.navigator.userAgent;
  const msie = ua.indexOf('MSIE');
  const trident = ua.indexOf('Trident/');

  if (msie > 0 || trident > 0) {
    hasError = true;
    error.msg = 'IE is not supported.';
  }
  if (hasError) _handleError(error);
};
