import { App, Plugin, install as install2 } from 'vue-demi';

install2();

// Import vue components
import * as components from './lib/index';
import CumulioService from './lib/cumulio-service';
// install function executed by Vue.use()
const install: Exclude<Plugin['install'], undefined> = (app: App) => {
  const entries = Object.entries(components);
  for (const [componentName, component] of entries) {
    app.component(componentName, component);
  }
  // app.component("cumulioService", CumulioService as Object)
  // app.config.globalProperties.$cumulioService = CumulioService;
};

// Create module definition for Vue.use()
export default install;

// To allow individual component use, export components
// each can be registered via Vue.component()
export * from './lib/index';
export { CumulioService };
