import { v4 as uuidv4 } from '@lukeed/uuid';
import { formatDashboardList } from '../helpers/utils';

// TODO: change function signature so that iframe is passed as a parameter and not contentWindow
export const getAccessibleDashboards = (dashboardComponent, iframe) => {
  if (
    (dashboardComponent.dashboardId || dashboardComponent.dashboardSlug) &&
    iframe?.contentWindow
  ) {
    const requestId = uuidv4();
    iframe.contentWindow.postMessage(
      {
        action: 'getAccessibleDashboards',
        _version: dashboardComponent.VERSION,
        requestId,
      },
      '*'
    );
    if (dashboardComponent.VERSION.includes('ngx')) {
      return Promise.resolve();
    }
    return new Promise((resolve, reject) => {
      dashboardComponent.promises[requestId] = { resolve, reject };
      // Check if promise has been resolved after 2 seconds
      setTimeout(() => {
        if (dashboardComponent.promises[requestId]) {
          const errorMessage = 'getAccessibleDashboards request timed out.';
          dashboardComponent.promises[requestId].reject(
            new Error(errorMessage)
          );
          delete dashboardComponent.promises[requestId];
        }
      }, 2000);
    });
  } else {
    // do an API call to fetch data.
    const inputData = {
      action: 'get',
      version: '0.1.0',
      key: dashboardComponent.authKey,
      token: dashboardComponent.authToken,
      find: {
        attributes: ['id'],
        where: {
          id: dashboardComponent.authKey,
        },
        include: [
          {
            model: 'Integration',
            attributes: ['id'],
            include: [
              {
                model: 'Securable',
                where: { type: 'dashboard' },
                attributes: ['id', 'name', 'modified_at'],
                order: [['modified_at', 'desc']],
              },
            ],
          },
        ],
      },
    };
    const promiseForFetch = new Promise((resolve, reject) => {
      void (async () => {
        try {
          const response = await window.fetch(
            `${String(dashboardComponent.params.apiHost)}0.1.0/authorization`,
            {
              method: 'POST',
              mode: 'cors',
              cache: 'no-cache',
              headers: {
                'Content-Type': 'application/json',
              },
              redirect: 'follow',
              body: JSON.stringify(inputData),
            }
          );
          const data = await response.json();
          const dashboardList = data.rows.length > 0
          ? formatDashboardList(data.rows[0].integration.securables)
          : [];
          if (dashboardComponent.VERSION.includes('ngx')) {
            dashboardComponent.getAccessibleDashboardsEvent.next(dashboardList);
          }
          resolve(
            dashboardList
          );
        } catch (error) {
          reject(error);
          dashboardComponent.getAccessibleDashboardsEvent.next([]);
        }
      })();
    });

    if (dashboardComponent.VERSION.includes('ngx')) {
      return Promise.resolve();
    } else {
      return promiseForFetch;
    }
  }
};
