export const addTrailingSlash = (serverUrl: string) =>
  serverUrl.endsWith('/') ? serverUrl : `${serverUrl}/`;

export const formatDashboardList = (
  dashboardList: any[]
): {
  id: string;
  slug?: string;
  modifiedAt: string;
  name?: { [language: string]: string };
}[] => {
  dashboardList = dashboardList || [];
  return dashboardList.map((s) => ({
    id: s.id,
    name: s.name ?? {},
    slug: s.integrationAccessRight?.slug ?? null,
    modifiedAt: s.modified_at,
  }));
};

export const VERSION = '0.0.8-vue-cumulio';

export const sanitizeUrl = (url: string) => {
  const invalidPrototcolRegex = /^(%20|\s)*(javascript|data)/im;
  const ctrlCharactersRegex = /[^\u0020-\u007E]/gim;
  const urlSchemeRegex = /^([^:]+):/gm;
  const relativeFirstCharacters = new Set(['.', '/']);
  const isRelativeUrlWithoutProtocol = (relativeUrl: string) =>
    relativeFirstCharacters.has(relativeUrl[0]);
  if (!url) return 'about:blank';
  const sanitizedUrl = url.replace(ctrlCharactersRegex, '').trim();
  if (isRelativeUrlWithoutProtocol(sanitizedUrl)) return sanitizedUrl;
  const urlSchemeParseResults = sanitizedUrl.match(urlSchemeRegex);
  if (!urlSchemeParseResults) return sanitizedUrl;
  const urlScheme = urlSchemeParseResults[0];
  if (invalidPrototcolRegex.test(urlScheme)) return 'about:blank';
  return sanitizedUrl;
};
