import defaultsDeep from 'lodash-es/defaultsDeep';
import cloneDeep from 'lodash-es/cloneDeep';
import { _handleError } from '../helpers/error';
import { DEFAULT_PARAMETERS } from '../helpers/default-parameters';

// 1. extend & test validity params + set itemEmbedding
export const _extendAndValidate = (dashboardContent) => {
  // remove null params before extend, not to let null override defaults remove not used params as well
  const defaultKeys = Object.keys(DEFAULT_PARAMETERS);
  for (const p of defaultKeys) {
    if (dashboardContent.params[p] === null) delete dashboardContent.params[p];
  }

  const errorMsgs = [];

  // save original params
  const original = cloneDeep(dashboardContent.params);
  dashboardContent.params.original = original;

  // extend with the default parameters before doing a check
  dashboardContent.params = defaultsDeep(
    dashboardContent.params,
    DEFAULT_PARAMETERS
  );

  if (
    (!dashboardContent.params.dashboardId ||
      typeof dashboardContent.params.dashboardId !== 'string') &&
    (!dashboardContent.params.dashboardSlug ||
      typeof dashboardContent.params.dashboardSlug !== 'string')
  ) {
    if (dashboardContent.params.editMode === 'editFull') dashboardContent.params.dashboardId = 'new';
    else {
      delete dashboardContent.params.dashboardId;
      delete dashboardContent.params.dashboardSlug;
    }
  }

  // check strings that can not be empty
  for (const p of ['appServer', 'language', 'apiHost']) {
    if (typeof dashboardContent.params[p] !== 'string') {
      delete dashboardContent.params[p];
      dashboardContent.params.warning = true;
      errorMsgs.push(`${p} needs to be of type string.`);
    }
  }
  // check strings or empty
  for (const p of [
    'key',
    'token',
    'screenMode',
    'timezoneId',
    'itemId',
    'container',
  ]) {
    if (
      typeof dashboardContent.params[p] !== 'string' &&
      dashboardContent.params[p] !== null &&
      dashboardContent.params[p] !== undefined
    ) {
      delete dashboardContent.params[p];
      dashboardContent.params.warning = true;
      errorMsgs.push(`${p} needs to be of type string or empty.`);
    }
  }
  if (
    dashboardContent.params.screenMode &&
    !['mobile', 'tablet', 'desktop', 'largeScreen', 'fixed', 'auto'].includes(
      dashboardContent.params.screenMode
    )
  ) {
    dashboardContent.params.warning = true;
    dashboardContent.params.screenMode = 'auto';
    errorMsgs.push(
      'screenMode needs to be either "mobile", "tablet", "desktop", "largeScreen", "fixed" or "auto"'
    );
  }
  // set itemEmbedding if item
  if (dashboardContent.params.itemId)
    dashboardContent.params.itemEmbedding = true;
  for (const p of ['itemDimensions', 'loader']) {
    if (
      typeof dashboardContent.params[p] !== 'object' &&
      dashboardContent.params[p] !== null
    ) {
      delete dashboardContent.params[p];
      dashboardContent.params.warning = true;
      errorMsgs.push(`${p} needs to be of type object or empty.`);
    }
  }
  // check boolean
  if (typeof dashboardContent.params.switchScreenModeOnResize !== 'boolean') {
    dashboardContent.params.warning = true;
    dashboardContent.params.switchScreenModeOnResize = true;
    errorMsgs.push('switchScreenModeOnResize needs to be of type boolean.');
  }
  // check itemDimensions
  if (dashboardContent.params.itemDimensions) {
    for (const p of ['width', 'height']) {
      if (
        !['string', 'number'].includes(
          typeof dashboardContent.params.itemDimensions[p]
        ) &&
        dashboardContent.params.itemDimensions[p] !== null
      ) {
        dashboardContent.params.warning = true;
        errorMsgs.push(
          `itemDimensions ${p} needs to be of type string, number or empty.`
        );
      }
    }
  }
  // check loader
  if (dashboardContent.params.loader) {
    for (const p of [
      'background',
      'fontColor',
      'spinnerColor',
      'spinnerBackground',
    ]) {
      if (
        typeof dashboardContent.params.loader[p] !== 'string' &&
        dashboardContent.params.loader[p] !== null
      ) {
        delete dashboardContent.params[p];
        dashboardContent.params.warning = true;
        errorMsgs.push(
          `loader property ${p} needs to be of type string or empty.`
        );
      }
    }
  }

  // extend again with default parameters to make up for the deleted invalid properties
  dashboardContent.params = defaultsDeep(
    dashboardContent.params,
    DEFAULT_PARAMETERS
  );
  // check screenMode in list & set itemDimensions if itemEmbedding
  if (
    dashboardContent.params.screenMode &&
    dashboardContent.params.screenMode !== 'auto'
  )
    dashboardContent.params.switchScreenModeOnResize = false;
  if (dashboardContent.params.itemDimensions.width !== 'auto')
    dashboardContent.params.itemDimensions.width =
      Number.parseInt(
        String(dashboardContent.params.itemDimensions.width).replace('px', ''),
        10
      ) || 'auto';
  if (dashboardContent.params.itemDimensions.height !== 'auto')
    dashboardContent.params.itemDimensions.height =
      Number.parseInt(
        String(dashboardContent.params.itemDimensions.height).replace('px', ''),
        10
      ) || 'auto';
  dashboardContent.params.itemEmbedding = dashboardContent.params.itemId
    ? true
    : false;

  if (dashboardContent.params.error || dashboardContent.params.warning)
    _handleError({ msg: errorMsgs.join('\n') }, dashboardContent.params);
};
